<template>
  <v-data-table :headers="headers"
                :items="data"
                item-key="id"
                :search="search"
                v-model="selected"
                select-all
                @click.native.stop="updateParent"
                :rows-per-page-items="rowsperpage"
                class="px-2">

    <v-progress-linear slot="progress"
                       color="primary"
                       indeterminate
                       height="5"></v-progress-linear>
    <template slot="items"
              slot-scope="props">
      <tr>
        <td>
          <v-checkbox v-model='props.selected'
                      primary
                      hide-details
                      color="secondary"
                      class="text-xs-center"
                      @click.native.stop="updateParent"></v-checkbox>
        </td>
        <td class="text-xs-left px-1">{{ props.item.port }}</td>
        <td class="text-xs-left px-1">{{ props.item.location }}</td>
        <td class="text-xs-left px-1">{{ props.item.uri }}</td>
        <td class="text-xs-left px-1">{{ props.item.application }}</td>
        <td class="text-xs-left px-1">{{ props.item.environment }}</td>
        <td class="text-xs-left px-1">{{ props.item.orderedDelivery }}</td>
        <td class="text-xs-left px-1">{{ props.item.status }}</td>
        <td class="text-xs-center px-3">
          <v-switch hide-details
                    color="tfggreen"
                    :input-value="props.item.status == 'Started' ? true : false"
                    @click.stop></v-switch>
        </td>
      </tr>
    </template>
  </v-data-table>
</template>

<script>
import { bus } from "@/main";
import { DataTableMixin } from "@/components/Mixins/DataTableMixin";

export default {
  mixins: [DataTableMixin],
  props: ["data", "search"],
  data() {
    return {
      headers: [
        {
          text: "Port",
          align: "left",
          sortable: true,
          value: "port",
          class: "pl-1"
        },
        {
          text: "Feed",
          align: "left",
          sortable: true,
          value: "location",
          class: "pl-1"
        },
        {
          text: "URI",
          align: "left",
          sortable: true,
          value: "uri",
          class: "pl-1"
        },
        {
          text: "Application",
          align: "left",
          sortable: true,
          value: "application",
          class: "pl-1"
        },
        {
          text: "Environment",
          align: "left",
          sortable: true,
          value: "environment",
          class: "pl-1"
        },
        {
          text: "OD",
          align: "left",
          sortable: true,
          value: "orderedDelivery"
        },
        {
          text: "Status",
          align: "left",
          class: "pl-1",
          value: "status"
        },
        {
          text: "",
          align: "center",
          class: "pl-1",
          value: ""
        }
      ],
      selected: []
    };
  },
  methods: {
    updateParent() {
      this.$emit("selectall", this.selected);
    },
    clearedSelected() {
      this.selected = [];
    }
  },

  mounted() {
    bus.$on("ClearBizSelected", this.clearedSelected);
  }
};
</script>
