<template>
    <section class="checkFeeds pt-2 mx-3">
        <div id="introduction"
             class="headline primary--text text-xs-center">
            <div>
                <div>
                    <p>Biztalk 2016 Test Feeds</p>
                </div>
            </div>
        </div>
        <v-card>
            <v-card-title>
                <v-layout align-start
                          justify-space-between
                          row
                          fill-height>
                    <v-flex xs4>
                        <v-text-field append-icon="search"
                                      @click:append="SearchFeeds"
                                      clearable
                                      label="Lookup Feeds..."
                                      v-model="lookup"
                                      single-line
                                      hide-details></v-text-field>
                    </v-flex>
                    <v-flex xs1>
                        <v-progress-circular indeterminate
                                             color="primary"
                                             class="mt-3 ml-0"
                                             v-if="this.GetLoading">
                        </v-progress-circular>
                    </v-flex>
                    <v-flex xs3>
                        <div v-if="selected.length > 0"
                             class="pl-0">
                            <v-btn color="tfgprimary"
                                   dark
                                   round>Start</v-btn>
                            <v-btn color="error"
                                   dark
                                   round
                                   @click="StopFeed()">Stop</v-btn>
                        </div>
                    </v-flex>
                    <v-flex xs4>
                        <v-text-field v-model="search"
                                      prepend-icon="filter_list"
                                      label="Filter results"
                                      single-line
                                      hide-details></v-text-field>
                    </v-flex>
                </v-layout>
            </v-card-title>
            <bizTable :data="this.GetFeeds"
                      :search="search"
                      @selectall="selected = $event">
            </bizTable>
        </v-card>
    </section>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import bizTable from "@/components/Biztalk/table";

export default {
  components: {
    bizTable
  },
  data() {
    return {
      lookup: "",
      search: "",
      selected: []
    };
  },
  methods: {
    ...mapActions(["StoreFeedsAct"]),
    SearchFeeds() {
      this.StoreFeedsAct(this.lookup);
    }
  },
  computed: {
    ...mapGetters(["GetFeeds", "GetLoading"])
  },
  created() {
    this.StoreFeedsAct(this.lookup);
  }
};
</script> 

<style lang="scss" scoped>
p {
  margin-bottom: 0px;
}
</style>
